import { Inline, Node } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import Image from '../models';

export default {
  match(node: Node): boolean {
    return Image.isImage(node);
  },

  convert(node: Inline): JsonML {
    // @ts-ignore
    return ['img', node.data];
  },
};
